.SimpleCaptureImageList {
    display: flex;
    overflow: auto;
}
  
.SimpleCaptureImageList::-webkit-scrollbar {
    display: none;
}

.MainSceneRoot {
    width: 100%;
    background-color: white;
}

.MainSceneRoot::-webkit-scrollbar {
    display: none;
}

.CameraCaptureEffect {
    position: fixed;
    height: 100%;
    width: 100%;
    left: 0%;
    top: 0%;
    background : white;
    opacity: 1;
    display: none;
}

.CameraCaptureAnimation {
    display: block;
    animation: CameraCaptureAnimationKeyframe 1s linear;
}

/*
blink 1s linear infinite alternate
*/
@keyframes CameraCaptureAnimationKeyframe {
    from {
      opacity: 1;
      display: block;
    }
    to {
      opacity: 0;
      display: none;
    }
  }


ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
::-webkit-scrollbar {
    /* width: 0px;
    background: transparent; */
    display: none;
  }

  .gridView-filter-content {
    overflow: hidden;
    overflow-y: scroll;
    margin-top: 18px;
    max-height: calc(100vh - 150px);
    /* max-height: calc(100vh - 270px); */
  }
  .gridView-play_quest {
    display: flex;
    flex-wrap: wrap;
    padding: 0px 1%;
    /* background-color: blue; */
  }
  .gridView-play_quest li {
    width: 185px;
    height: 185px;
    margin: 0px 1.6% 24px;
    text-align: center;
    /* background-color: red; */
  }
  .gridView-play_quest_thumb {
    position: relative;
    width: auto;
    max-width: 185px;
    height: 185px;
    margin: 0 auto;
    /* background-color: pink; */
    /* background-color: pink; 71px*/
  }
  .gridView-play_quest_thumb_img {
    position: absolute;
    width: 100%;
    left: 0%;
    top: 0%;
  }
  .gridView-play_quest_thumb_is_selected_img {
    position: absolute;
    width: 100%;
    left: 0%;
    top: 0%;
    filter: brightness(150%) grayscale(80%);
  }

  .gridView-play_quest_thumb-check {
    position: absolute;
    width: 30%;
    height: auto;
    left: 80%;
    top: 85%;
    opacity: 0.8;
    transform: translate(-50%, -50%);
  }

  .gridView-play_quest_thumb_check {
    position: absolute;
    width: 20px;
    height: 20px;
    margin: 0px;
  }
  .gridView-play_quest_thumb_check img {
    position: absolute;
    width: auto;
    height: 100%;
    transform: translate(0%, -200%);
  }
  .gridView-play_quest_label {
    display: block;
    margin: 12px 0 0;
    font-size: 9px;
    line-height: 1.2;
    color: white;
  }






.simple_capture_left_title {
